.loader {
  width: 80px;
  height: 80px;
  box-sizing: border-box;
  transition: opacity .75s ease infinite;
  border-top: 6px solid #dcdcdc;
  border-right: 6px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: rotation 1s linear infinite;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  border-bottom: 6px solid rgba(0, 0, 0, 0);
  border-left: 6px solid #ff6b08;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background: rgba(0, 0, 0, .25);
  transition: visibility .45s cubic-bezier(.4, 0, .2, 1), opacity .45s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

